module.exports = "<!-- metadata\ntitle: Appian's iOS fastlane build system\ntags: fastlane, Ruby, RSpec, Xcode, xcodebuild, XCTest, Bash\n-->\n\n#### Covnerted duplicated, non-modular, confusing Bash-based build and test system to fastlane \n2016\n\n##### Why did I enjoy this project?\n\nI enjoyed this project because it made our build system simple, streamlined, reliable, and easy to understand, improve, and maintain. Additionally, I found Ruby to be a fascinating language to use and writing unit tests with it to be pure joy.\n\n##### Background\n\nAppian's iOS build system used to be a collection of confusing, duplicated, and poorly organized Bash scripts across 3 repositories. Read how more about how I fixed it here, in my article: [Rescue your mobile builds from madness using fastlane](https://medium.com/appian-engineering/rescue-your-mobile-builds-from-madness-using-fastlane-cf123622f2d3).\n"

