module.exports = "<!-- metadata\ntitle: Cross-platform Multithreading Framework\ntags: C++, Win32 threading, POSIX threading\n-->\n\n#### Created a Macintosh and Windows C++ multithreading framework with workers and thread pools.\n2002\n\n##### Why did I enjoy this project?\nI enjoyed this project because I was able to learn new technology and make it available to other Software Engineers in the department who needed multithreading but didn't have the time to learn it.\n\n##### Background\n\nAs part of a networked license validator, I was required to add multithreading code to perform network checks and validation off of the main thread. To make the deadline, I had to write platform specific implementations.\n\nI was not happy that there were two different implementations of the same patterns which could not be reused.\n\nOn my own time and of my own initiative, I developed cross-platform threading and synchronizing classes that wrapped the native API calls. This was before Mac OS was based on BSD Unix.\n\nWhen Apple changed the OS to run on Unix, it was a simple matter of changing the Mac implementation of the classes for the library to continue working in the same way.\n\n"

