module.exports = "<!-- metadata\ntitle: ReXYZ\ntags: C++, Metrowerks CodeWarrior IDE, Metrowerks PowerPlant framework\n-->\n\n#### A Mac OS application to reconstruct 3D anatomical organs from 2D image slices of a human body.\n1998\n\n##### Why did I enjoy this project?\nI enjoyed this project because I was given an opportunity to learn 3D programming and deliver an actual working application.\n\n##### Background\nAfter successfully converting the Department of Anatomy and Cell Biology's static website into an interactive website using C++ CGI backend applications, JavaScript and Java Applets for the front-end, the department asked me to create an application to reconstruct 3D human organs and display them in a way that users could interact with them.\n\nThey gave me the time to learn 3D geometry, digital contour-tracing, point clouds, and Gouraud Shading.\n\nTwo months later, I provided them the application that did just that. \n\nThe application would allow users to select organs from images taken from thing slices of a human body using a magenetic lasso to select the countours of the organ using digital processing algorithms.\n\nAfter selecting the contours of an organ, the application would then use the point clouds of the selected organ contours to reconstruct a 3D organ that was rendered with Gouraud Shading, and rotateable in 3D space to learn more about the organ.\n"

