module.exports = "<!-- metadata\ntitle: blackberry_mam\ntags: Ruby, fastlane, xcodebuild, XCTest, XCUIText, Xcode, XML, BlackBerry Dynamics SDK, Objective-C\n-->\n\n#### [A fastlane plugin](https://github.com/lyndsey-ferguson/blackberry_mam) for BlackBerry iOS Mobile Application Management SDK integration.\n2017\n\n##### Why did I enjoy this project?\n\nI enjoyed this project because I made something that can be used by developers outside of Appian to customize their build system to integrate their iOS apps with the [Blackberry MDM](https://us.blackberry.com/enterprise/emm/mobile-device-management) SDK.\n\n##### Background\n\nAppian provides an iOS application to interface with their low-code application platform. They also provide an iOS application that is integrated with the Blackberry Dynamics MDM SDK for customers that want even more security by locking down applications and adding in the ability to erase the secure storage in the event of device loss.\n\nTo provide an iOS app integrated with the Blackberry SDK, Appian's fastlane build system can use the plugin to modify the Info.plist, and then change the linking flags and project dependencies to work with the SDK and turn off standard Push Notifications.\n\nThis keeps the build system simple and streamlined, only taking a slight detour to integrate the app with the Blackberry SDK.\n"

