module.exports = "<!-- metadata\ntitle: Mac OS 9 Resource Browser\ntags: C++, Mac OS 9, Codewarrior PowerPlant\n-->\n\n#### Converted floating window for resource management to \"modern\" OS 9 technology\n2000\n\n##### Why did I enjoy this project?\nI enjoyed this project because it was one of my first big projects of converting custom UX code into cross-platform code that used an industry standard framework. Not only did I learn a lot, but also made the code base more approachable for new developers.\n\n##### Background\n[Vectorworks](https://www.vectorworks.net/en), the Mac and Windows [CAD](https://en.wikipedia.org/wiki/Computer-aided_design) application has many _resources_ such as many different fill patterns and types for shapes, predefined shapes and objects, etc.\n\nThe application document can contain **many** resources. So many that it becomes difficult to find what you want. That's where the [Resource Browser](http://app-help.vectorworks.net/2016/eng/VW2016_Guide/ResourceBrowser/The_Resource_Browser.htm#XREF_76980_Resource_Libraries) comes in: it helps sort, categorize, and manage resources.\n\nPreviously, the Resource Browser for Mac OS 8 was written with custom classes that were not easily modified. When Vectorworks was upgraded for Mac OS 9, I rewrote the GUI code using [Codewarrior's PowerPlant](https://en.wikipedia.org/wiki/PowerPlant), a C++ framework that worked more seamlessly with that version of the Mac OS APIs."

