module.exports = "<!-- metadata\ntitle: setup_fragile_tests_for_rescan\ntags: Ruby, fastlane, xcodebuild, XCTest, XCUIText, Xcode, XML\n-->\n\n#### A fastlane plugin that suppresses passing tests so that only failing tests are run again.\n2017\n\n##### Why did I enjoy this project?\nI enjoyed this project because it taught me a lot about Apple's testing infrastructure, and it reduced the effects of a fragile test environment and fragile tests that interfere with each other: allowing developers to focus on fixing code that truly broke tests.\n\n##### Background\nWhen I became part of the Mobile team as an iOS Software Engineer, I was frustrated that the existing automated tests had many failures. \n\nAt one point, we, as a team, spent a lot of time trying to make sure that our code worked correctly, that our tests were written correctly, and then we spent a lot of time trying to work around the issues that seemed totally due to Apple's test infrastructure.\n\nEven after a large effort of trying to stabilize our tests and the test infrastructure, random tests would fail. Each run would deliver a different result so we could never be certain if we had fixed the underlying problem or not.\n\nThat's when I created the fastlane plugin, `setup_fragile_tests_for_rescan` to temporarily suppress the passing tests in the Xcode Project Scheme so that subsequent runs of the tests would focus on failing tests. After three runs of suppressing passing tests and re-testing only failing tests, the only tests that failed were ones that found a real problem.\n\nThe result was that we stopped wasting time in 'wack a mole', trying to fix fragile tests, and focused more time on delivering features that our customers want."

