module.exports = "<!-- metadata\ntitle: Win32 Vectorworks\ntags: QuickTime Library, Win 32 APIs, C, C++\n-->\n\n#### Complete plan and guidance to convert Vectorworks for Windows from Mac emulation APIs to native Win32 APIs.\n2013\n\n##### Why did I enjoy this project?\nI enjoyed this project because I drew up the plan that lead the team working on it to quickly stop using the Apple Quicktime library as a source of API emulation. This stabilized the entire application as the single Quicktime runtime that serviced the calls would become corrupt requiring the Windows computer to be restarted.\n\n##### Background\nAs I mentioned in the project, \"Modern Mac OS X Vectorworks\", Vectorworks used Apple's Quicktime library to emulate drawing APIs for the Windows version of the application.\n\nThis introduced two problems: \n\n1. the single Quicktime runtime servicing the calls could become corrupt by any client using the library APIs incorrectly. When this happened, the only fix was to restart the computer; this did not give Vectorworks a very good reputation when Support had to tell the customer to restart the computer, or to not use another piece of software that used the Quicktime library.\n2. Apple had never intended for the library to be used as an entire API emulation layer and was not making any effort to support newer and newer versions of Windows. There was an unspoken end-of-life for the library and Vectorworks had to stop using it.\n\nSo, foreseeing the end of the support, and the problems that we had with this library, I drew up the plan of how we could replace our use of the APIs in stages, without disrupting the customer.\n\nWhile I was leading the transition from the old Carbon APIs on the Mac to the Objective-C Cocoa framework, I also provided support and guidance to another team that transitioned off the Quicktime APIs to Win32 APIs.\n\nI was doubly vindicated when Apple discontinued support for the library, and the Department of Homeland Security [issued a security alert](https://forum.vectorworks.net/index.php?/topic/43906-quicktime-for-windows-end-of-life-important/) to users of the Quicktime library."

