module.exports = "<!-- metadata\ntitle: Gracilis Website\ntags: JavaScript, HTML, C++, CGI\n-->\n\n#### An interactive education website for anatomy students\n1996\n\n##### Why did I enjoy this project?\n\nI really enjoyed working on this project because I was entrusted and empowered. I was given license to learn new and, at the time, modern, technologies on the job. \n\nI was entrusted to provide advice on which technogoies to use after I provided the results of my research. \n\nI was given the time to develop something the best that I could given my knowledge at the time: I was not forced to cut corners.\n\n##### Background\n\nWhile I was working for the Department of Consulting and Development at the University of Saskatchewan, the Department of Anatomy and Cell Biology contracted me to complete their static website that provided educational materials to their students.\n\nI had never worked with HTML before, so I was very fortunate that they allowed me to spend time learning from a book that I had bought for myself; this was back in the day **before** there were online resources dedicated to teaching HTML.\n\nAs I completed the website far faster than they had anticipated and there was plenty of time before the contract ended, they asked me: can you make it more dynamic, more interactive?\n\nI said I could, as I had touched briefly upon [CGI](https://en.wikipedia.org/wiki/Common_Gateway_Interface) and JavaScript while learning HTML.\n\nI purchases a book on developing CGI applications using C++ as the first step. I had considered [Java Applets](https://en.wikipedia.org/wiki/Java_applet) for the interactivity, but that required the students to install plug-ins and we wanted to keep the number of obstacles low.\n\nWith the CGI technology, I created an image layering application that would allow the anatomy students to select the layers of a body part to view: skin, muscle, tendons, veins, bones, etc. Based on the selection, the application would put together the corresponding layers to present a new image to the student.\n\nI then purchases a book on JavaScript and developed randomly generated tests that students could use to test their knowledge of the material that they studied. Each student would have their own account so that they could leave the site and return to continue with their lessons and tests. I also used the JavaScript to make a more advanced and attractive navigation experience.\n\nLater on, we added videos of various medical procedures and 3D videos that could be used to rotate around organs using [Quicktime VR](https://en.wikipedia.org/wiki/QuickTime_VR).\n\n![An example of an animation video on the site][dental_sedative]\n\nFor a blast from the past, visit [this page](https://onlinelibrary.wiley.com/doi/full/10.1002/(SICI)1097-0185(20000415)261:2%3C57::AID-AR5%3E3.0.CO;2-R) that includes some discussion on the site at the Wiley Online Library.\n\n[dental_sedative]: https://wol-prod-cdn.literatumonline.com/cms/attachment/f19497b8-7464-466d-bb7c-21d46db30e20/mfig002.jpg \"An example of an animation video on the site\""

