module.exports = "<!-- metadata\ntitle: react-native-babycare-app\ntags: React Native, Redux, JavaScript\n-->\n\n#### A mobile application for parents to track their childrens vaccinations.\n2017\n\n##### Why did I enjoy this project?\nI enjoyed this project because it was my own idea to develop an application to help with children's vaccinations in Argentina.\n\n##### Background\nI had converted a GWT based web application framework to React and had heard of React Native. I wanted to develop something with React Native to get some experience and so I decided to write this application in React Native. \n\nOnce I got the hang of it, I really enjoyed using Redux and React-Native to quickly develop the application. Debugging was a little tricky as the wrong configuration can break your application and the root cause of that breakage can be difficult to determine.\n\nI decided to release it as Open Source because I thought it could be valuable as a lesson to other developers.\n\n[Babycare](https://github.com/lyndsey-ferguson/react-native-babycare-app)\n"

